import React from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import CalendarEmbed from '../components/CalendarEmbed';
import { useNavigate } from 'react-router-dom';

function Home() {

  const navigate = useNavigate();

  const handleLearnMore = () => {
    navigate('/research', { state: { smoothScroll: true } });
  };


  return (
  
    <>
    <Container className="my-5">
      <Row>
        <Col>
          <h1>Bienvenue sur le laboratoire de recherche de Phaedra Royle</h1>
          <br />
          <p>Pour faire avancer les connaissances en Orthophonie.</p>
          <Button variant="primary" onClick={handleLearnMore}>En savoir plus</Button>
          <img src={process.env.PUBLIC_URL + '/images/group.png'} alt="Research Lab" className="img-fluid" />
        </Col>
      </Row>
      
    </Container>

    <Container fluid className="my-5 p-0" style={{ backgroundColor: '#F1F2FA' }}>
      
      
      <Container className="my-5">
        <br />
        <h2 className="custom-margin-top"> Évènements </h2>
        <h2>à venir</h2>

          <Row className="mt-5">
            <Col>
              <CalendarEmbed/>
            </Col>
          </Row>
          
        </Container>

    </Container>

    <Container fluid className="my-5 p-0">
      <Container className="my-5">
        <br />
        <h2 className="custom-margin-top"> A venir </h2>
        


        </Container>

    </Container>

    </>

    
  );
}

export default Home;